@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
}
/**Ширина экрана**/
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 13px;
  line-height: 24px;
  font-weight: 400;
  color: #444;
}

.page__body {
  max-width: 767px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__body {
    max-width: 1199px;
  }
}
@media (min-width: 1150px) {
  .page__body {
    max-width: 1800px;
  }
}

/**Главная**/
.main-header {
  display: block;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 768px) {
  .main-header {
    display: flex;
    background-color: #f9f9f9;
  }
}

@media (min-width: 768px) {
  .main-header::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 88px;
    background-color: #fff;
    top: 0;
    left: 0;
  }
}

.main-header__logo-link {
  display: block;
  width: max-content;
  margin: 22px auto;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-header__logo-link {
    margin: 0;
    position: absolute;
    top: 22px;
    left: 48px;
    z-index: 3;
  }
}
@media (min-width: 1150px) {
  .main-header__logo-link {
    margin: 0;
    position: absolute;
    top: 9px;
    left: 50%;
    z-index: 3;
    transform: translateX(-50%);
  }
}

.main-header__logo {
  display: block;
  z-index: 2;
}

.main-nav__toggle {
  position: absolute;
  width: 20px;
  height: 14px;
  top: 32px;
  right: 30px;
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 20px;
  height: 2px;
  background-color: #000;
  box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: minmax(185px, 285px) minmax(185px, 285px) 82px minmax(300px, 500px);
  }
}
@media (min-width: 1150px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: minmax(234px, 350px) 220px minmax(295px, 400px) 82px minmax(300px, 500px);
  }
}

.main-nav__item {
  position: relative;
  box-sizing: border-box;
  padding: 28px 20px 27px 76px;
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__item {
    border: none;
    padding: 0;
  }
}

.main-nav__item--search {
  position: absolute;
  top: 32px;
  left: 32px;
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(3) {
    padding: 0;
    height: 88px;
    width: 82px;
    order: -1;
    border-left: 1px solid #ececec;
    grid-column: 3/4;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(3) {
    padding: 0;
    height: 88px;
    width: 82px;
    border-left: 1px solid #ececec;
    grid-column: 4/5;
    z-index: 2;
  }
}

@media (min-width: 768px) {
  .main-nav__item-text--search {
    display: none;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(1) {
    padding: 22px 0 22px 50px;
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(1) {
    padding: 32px 35px 32px 50px;
    grid-column: 1/2;
    order: -1;
    z-index: 2;
    border-right: 1px solid #ececec;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(2) {
    padding: 22px 5px 22px 30px;
    grid-column: 2/3;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(2) {
    padding: 32px 25px 32px 35px;
    grid-column: 2/3;
    order: -1;
    z-index: 2;
    border-right: 1px solid #ececec;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(4) {
    box-sizing: border-box;
    order: -1;
    padding: 32px 0 32px 85px;
    border-left: 1px solid #ececec;
    grid-column: 4/5;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(4) {
    box-sizing: border-box;
    padding: 32px 0 32px 85px;
    border-left: 1px solid #ececec;
    grid-column: 5/6;
    z-index: 2;
  }
}

.main-nav__item:nth-child(4)::after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: #ececec;
  width: 10000px;
  bottom: 0;
  right: 0;
}

.main-nav__item:nth-child(4)::before {
  content: "";
  position: absolute;
  height: 1px;
  background-color: #ececec;
  width: 10000px;
  bottom: 0;
  left: 0;
}

@media (max-width: 768px) {
  .main-nav__item:nth-child(n+5) {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(n+5) {
    padding: 24px 25px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(n+5) {
    padding: 20px 25px 20px 50px;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(5) {
    grid-column: 4/5;
    padding: 22px 50px 22px 35px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(5) {
    grid-column: 5/6;
    padding: 20px 50px 20px 30px;
    order: 2;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(6) {
    display: none;
  }
}
@media (min-width: 768px) and (min-width: 1150px) {
  .main-nav__item:nth-child(6) {
    display: block;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(7) {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__item:nth-child(7) {
    grid-column: 2/3;
    padding: 20px 25px 20px 35px;
    display: block;
  }
}

.main-nav__item--cart {
  position: absolute;
  top: 30px;
  left: 30px;
}
@media (min-width: 768px) {
  .main-nav__item--cart {
    top: 35px;
  }
}

.main-nav__link {
  text-decoration: none;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000;
}
@media (min-width: 768px) {
  .main-nav__link {
