}

.type__control:active .type__control-mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.type__control-input:disabled + .type__control-mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.type__control-input[type=radio]:checked + .type__control-mark::before {
  position: absolute;
  content: "";
  width: 23px;
  height: 23px;
  background-color: #62D1BA;
  border-radius: 50%;
  top: 8px;
  left: 8px;
}

